


import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


@Injectable()
export class OffersService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getOffersList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/vendor-offers`, { params: param });
  }

  addOffers(data): Observable<any> {
    return this.http.post(`${this.API_URL}/vendor-offers`, data);
  }

  editOffers(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/vendor-offers/${id}`, data);
  }

  deleteOffers(id): Observable<any> {
    return this.http.delete(`${this.API_URL}/vendor-offers/${id}`);
  }

  getOffersById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/vendor-offers/${id}`, { params: param });
  }




}

