import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { OffersListComponent } from './offers-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { OffersService } from 'src/app/service/offers.service';

const routes: Routes = [
  {
    path: '',
    component: OffersListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../offers-add/offers-add.module').then(m => m.OffersAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../offers-add/offers-add.module').then(m => m.OffersAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    OffersListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    OffersService
  ]
})

export class OffersListModule { }
